.john{
margin-top: 20px;
margin-bottom: 20px;
box-shadow: 19px 15px 22px 6px rgba(0,0,0,0.52);
justify-items:flex-start;
align-content: space-around;
align-items: center;
display: grid;
justify-content: left;
width: 15%;
border-radius: 10%;
z-index: 1;
}
.homework{
    margin-top: 35px;
    display: flex;
    padding-left: 40px;
    padding-top: 20px;
    justify-content:space-around;
    align-content: center;
    align-items: center;
    align-self: center;


}
.profile{
    justify-content: center;
    align-content: center;
    align-items: center;
    align-self: center;
    display: inline-block;

}
.listHobbies{
    text-align: start;
    gap: 0;
    justify-content: center;
    font-size: 13px;
    font-weight: bolder;
    align-items: center;
    padding-left: 20px;

}
h4{
    font-weight: bold;
    padding-bottom: 15px;
}
